.main-experience-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    width: 90%;
    .arrowLeft{
        margin-left: 5rem;
        text-align: -webkit-center;
    }
  }
  .experience-container {
    /* overflow-y: scroll;
    overflow-x: hidden; */
    margin-top: 3rem;
    max-width: 768px;
    max-height: 768px;
    margin-left: auto;
    margin-right: auto;
  }
  .experience-table {
    display: flex;
    flex-direction: column;
    margin: 2rem;
    li {
      border-radius: 3px;
      padding: 25px 30px;
      display: flex;
      justify-content: space-between;
      margin-bottom: 25px;
    }
    .table-header {
      background-color: #f8dc81;
      font-size: 14px;
      text-transform: uppercase;
      letter-spacing: 0.03em;
      text-align: center;
    }
    .table-row {
      background-color: #f8f1f1;
      box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.1);
      align-items: center;
      text-align: center;
    }
    .table-row:hover{
      background-color: #374045;
      transition: 0.4s ease;
      color: white;
    }
    .col-1 {
      flex-basis: 20%;
      font-weight: bold;
    }
    .col-2 {
      flex-basis: 20%;
    }
    .col-3 {
      flex-basis: 30%;
    }
    .col-4 {
      flex-basis: 50%;
    }
  }
  